
<template>
  <body>
  <div class="login-box">
      <input type="radio" id="create-acc" name="1" >
      <input type="radio" id="login-acc" name="1" checked>

      <div class="img-box">
      </div>

      <div class="signin-box">
          <div class="login-text">Login</div>

          <div class="login-inside">
              <div>
                  <div>
                      Username
                  </div>
                  <input type="text" v-model='username'>
              </div>
              <div>
                  <div>
                      Password
                  </div>
                  <input type="password" v-model='password'>

              </div>

              <div @click='request'>
                  <div class="login-btn">Login</div>
              </div>


              <label for="create-acc" class="create-text" @click='login=false, username="",password=""'>Create Account</label>
          </div>
      </div>

      <div class="signup-box">
          <div class="signup-text">Signup</div>

          <div class="login-inside">
              <div>
                  <div>
                      Username
                  </div>
                  <input type="text" v-model='username'>
              </div>
              <div>
                  <div>
                      Password
                  </div>
                  <input type="password" v-model="password">

              </div>

              <div @click='request'>
                  <div class="signup-btn">Sign in</div>
              </div>


              <label for="login-acc" class="create-text" @click='login=true, username="",password=""'>Login Account</label>
          </div>
      </div>
  </div>    
  </body>
</template>
<script>
import axios from 'axios';
export default {
  data(){
    return {
      login : true,
      username : '',
      password : ''
    }
  },
  methods : {
    request(){
      if(this.login){
        axios.get(`login?username=${this.username}&password=${this.password}`).then(data => {
          this.$toastr.s('Welcome ' + this.username)
          localStorage.setItem('token', data.data.token);
          localStorage.setItem('type', (data.data.admin) ? 'admin' : 'user');
          if(data.data.admin){this.$router.push("/admin")}else{this.$router.push("/");}
        }).catch(err =>{
           this.$toastr.e(err.response.data.Error)
        })
      }else{
        axios.get(`signup?username=${this.username}&password=${this.password}`).then(data => {
          this.$toastr.s('Welcome ' + this.username)
          localStorage.setItem('token', data.data.token);
          localStorage.setItem('type',  'user');
          this.$router.push("/");
        }).catch(err =>{
           this.$toastr.e(err.response.data.Error)
        })
      
      }
    }
  }
}
</script>
