<template>
  <body>
    <div @click="logout" class="logout-btn">
      Logout
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        style="enable-background: new 0 0 24 24"
        version="1.1"
        viewBox="0 0 24 24"
        xml:space="preserve"
      >
        <g id="info" />
        <g id="icons">
          <g id="exit2">
            <path
              d="M12,10c1.1,0,2-0.9,2-2V4c0-1.1-0.9-2-2-2s-2,0.9-2,2v4C10,9.1,10.9,10,12,10z"
            />
            <path
              d="M19.1,4.9L19.1,4.9c-0.3-0.3-0.6-0.4-1.1-0.4c-0.8,0-1.5,0.7-1.5,1.5c0,0.4,0.2,0.8,0.4,1.1l0,0c0,0,0,0,0,0c0,0,0,0,0,0    c1.3,1.3,2,3,2,4.9c0,3.9-3.1,7-7,7s-7-3.1-7-7c0-1.9,0.8-3.7,2.1-4.9l0,0C7.3,6.8,7.5,6.4,7.5,6c0-0.8-0.7-1.5-1.5-1.5    c-0.4,0-0.8,0.2-1.1,0.4l0,0C3.1,6.7,2,9.2,2,12c0,5.5,4.5,10,10,10s10-4.5,10-10C22,9.2,20.9,6.7,19.1,4.9z"
            />
          </g>
        </g>
      </svg>
    </div>
    <div class="post-box">
      <div class="post-show">
        <div class="main-posts">
          {{paragraph}}
        </div>
      </div>
      <div class="next-prev">
        <div class="prev-btn" @click="prev">Previous</div>
        <div class="next-btn" @click="next">Next</div>
      </div>
    </div>
    <div class="post-list">
      <div class="list-text">Posts</div>

      <div class="list-box">
        <div
          class="post-id"
          :class="(currentindex == eachpost) ? 'selected' : ''"
          v-for="eachpost in Object.keys(totalposts)"
          :key="eachpost"
          @click="paragraph = totalposts[eachpost].post,currentindex = eachpost"
        >
          #{{eachpost}}
        </div>
      </div>
    </div>
  </body>
</template>
<script>
import axios from 'axios';
export default {
    name : 'dashboard',
    data(){
        return {
            paragraph : 'No Post Found',
            totalposts : [],
            currentindex : 0
        }
    },
    created(){
        const headers = { headers : {
            'token' : localStorage.getItem('token')
        }}
        axios.get('post', headers).then(data => {
            if(data.data.length != 0){
                this.totalposts = (data.data)
                this.currentindex = 0
                this.paragraph = data.data[0].post
            }
        }).catch(err => {
           this.$toastr.e(err.response.data.Error)
        })
    },
    methods : {
        next(){
            if(this.currentindex < this.totalposts.length-1){
                this.paragraph = this.totalposts[++this.currentindex].post

            }else{
                this.$toastr.i('Ahh This Was Last Post :( ')
            }
        },
        prev(){
            if(this.currentindex != 0){
                this.paragraph = this.totalposts[--this.currentindex].post
            }else{
                this.$toastr.i('Sorry No Previous Post Found')
            }
        },
        logout(){
            localStorage.clear();
            this.$router.push('/login');
        }
    }
}
</script>
